/* -------------------------------------
    TABLE
------------------------------------- */

.table {
  width: 100%;
  border-collapse: collapse;
  &__description {
    font-size: 2rem;
    line-height: 1.35;
    letter-spacing: -0.08rem;
    font-family: $serif;
    @media all and (max-width: 650px) {
      font-size: 1.8rem;
      line-height: 1.5;
      letter-spacing: -0.05rem;
      font-family: $serif;
    }
  }
  &__results {
    width: 60%;
  }
}



